import React, { Component } from 'react'
import Dropzone from 'react-dropzone'
//import sha1 from 'sha1'
//import superagent from 'superagent'

class DropComponent extends Component {
    constructor(){
        super()
        this.state = {
            image: null,
            loading: false,
            dropzoneActive: false
        }
    }

    onDragEnter() {
        this.setState({
            dropzoneActive: true
        });
    }

    onDragLeave() {
        this.setState({
            dropzoneActive: false
        });
    }

    uploadFile(files){
        if(files.length == 0) {
            this.setState({loading: false});
            return;
        }

        //Add all dragged images to an array until you have reached the max
        var images = [];
        for (var i in files){
            if(i >= 5) break;
            images.push(URL.createObjectURL(files[i]));
        }

        this.props.onChange(images);
        this.setState({loading: false});
    }

    //The code below sends the read image to a image hosting service.
    uploadFileToHostingService(files){
        if(files.length == 0) {
            this.setState({loading: false});
            return;
        }
        const image = files[0]

        const cloudName = 'skybase'
        const url = 'https://api.cloudinary.com/v1_1/'+cloudName+'/image/upload'

        const timestamp = Date.now()/1000
        const uploadPreset = 'tzedmeu5'

        const paramsStr = 'timestamp='+timestamp+'&upload_preset='+
            uploadPreset+'cKTMYYiF247oFoCxgmQGITuRUdo'

        const signature = sha1(paramsStr)
        const params = {
            'api_key': '842212512699464',
            'timestamp': timestamp,
            'upload_preset': uploadPreset,
            'signature': signature
        }

        let uploadRequest = superagent.post(url)
        uploadRequest.attach('file', image)

        Object.keys(params).forEach((key) => {
            uploadRequest.field(key, params[key])
        })

        uploadRequest.end((err, resp) => {
            if (err){
                alert(err)
                return
            }

            console.log('UPLOAD COMPLETE: '+JSON.stringify(resp.body))
            const uploaded = resp.body

            this.props.onChange(uploaded.secure_url)
        })
    }

    render(){
        if(!this.state.loading) {
            return (
                <div>
                    <Dropzone
                        style={{position: "relative"}}
                        accept="image/jpeg, image/png"
                        onDrop={(x) => {this.setState({loading: true, dropzoneActive: false});
                                        this.uploadFile(x) }}
                        onDragEnter={this.onDragEnter.bind(this)}
                        onDragLeave={this.onDragLeave.bind(this)}
                    >
                        {this.state.dropzoneActive &&
                            (<div style={{backgroundColor: 'rgba(1, 1, 1, 0.5)',
                                          position: 'absolute',
                                          top: 0,
                                          right: 0,
                                          bottom: 0,
                                          left: 0,
                                          textAlign: 'center'}} />)}
                        {this.props.children}
                    </Dropzone>
                </div>
            )
        }
        else{
            return (
                <div>
                    Loading!
                </div>
            )
        }
    }
}

export default DropComponent



