import React, { Component } from 'react'

class ImagePanel extends Component {
    //Displaying all of the added images and the drop box if allowed
    render(){
        //Specifies how the images should be broken up
        var mapping = [{'full': true, 'url': this.props.images[0]}];
        for(var i=1, len=this.props.images.length; i<len; i+=2){
            mapping.push({'full': false, 'url': (i==len-1? ([this.props.images[i]]) :
                                                           ([this.props.images[i], this.props.images[i+1]]))});
        }
        if(this.props.dropEnabled && this.props.images.length < 5){
            if(mapping[mapping.length-1]['full'] || mapping[mapping.length-1]['url'].length > 1){
                mapping.push({'full': false, 'url': ["static/src/ImageDrop.png"]});
            }
            else{
                mapping[mapping.length-1]['url'].push("static/src/ImageDrop.png")
            }
        }

        var dim = this.props.dimensions;

        //Displays the images in a specialised format
        return(
            <div style={{display: 'inline-block'}}>
                {mapping.map(elm => (
                    elm['full'] ? (<img style={{float: 'left', width:dim[1], height:dim[1]}} key={elm['url']} src={elm['url']} />)
                        :(
                            <div style={{float: 'left'}} key={elm['url'][0]+", "+elm['url'][1]}>
                                <img style={{width:dim[1]/2, height:dim[1]/2}} src={elm['url'][0]} />
                                <br />
                                {elm['url'].length < 2
                                    ? <div style={{width:dim[1]/2, height:dim[1]/2}}><br /></div>
                                    : <img style={{width:dim[1]/2, height:dim[1]/2}} src={elm['url'][1]} />
                                }
                            </div>
                        )
                ))}
            </div>
        )
    }
}



export default ImagePanel


