import React, { Component } from 'react';
import onClickOutside from "react-onclickoutside";
import DatePicker from 'react-datepicker/dist/react-datepicker.min.js';
import PropTypes from 'prop-types/prop-types.min.js'
import moment from 'moment/moment.js';
import { get_location } from './AppUtilities'
import LocationSelector from './LocationSelector';

import 'react-datepicker/dist/react-datepicker.css';

class CustomDateInput extends React.Component {
  render () {
    return (
      <input
        className="example-custom-input"
        style={{width: '200px', 'font-size': '16px'}}
        onChange={() => {}}
        onClick={(e) => {e.preventDefault(); this.props.onClick(e)}}
        value={this.props.value}
      />
    )
  }
}
CustomDateInput.propTypes = { onClick: PropTypes.func, value: PropTypes.string };

class Input extends Component {
    constructor(props){
        super(props);

        this.state = {
            loc: "Locating...",
            pos: null,
            date: moment(),
            radius: 500
        };

        get_location((location, position) => {this.setState({loc: location, pos: position})});

        this.handleDateChange = this.handleDateChange.bind(this);
        this.handleLocationChange = this.handleLocationChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.handleTextChange = this.handleTextChange.bind(this);
        this.handleClickOutside = this.handleClickOutside.bind(this);
    }

    render(){
        return(
            <div>
                <div align="left" style={{float: 'left', width: '200px', height: '330px'}}
                     onClick={this.handleClickOutside}
                >
                    <form style={{float: 'left'}} onSubmit={this.handleSubmit}>
                        <h2>Location:</h2>
                        <textarea
                            style={{width: '200px', height: '50px', resize:'none', 'font-size': '16px'}}
                            id="Location"
                            onClick={() => {this.props.setMapOpen(true)}}
                            onChange={this.handleTextChange}
                            value={this.state.loc}
                        >Test</textarea>
                        <br /><br />
                        <h2>Date:</h2>
                        <DatePicker
                            customInput={<CustomDateInput />}
                            className="test"
                            dateFormat="DD/MM/YYYY"
                            selected={this.state.date}
                            maxDate={moment()}
                            minDate={moment().subtract(14, "days")}
                            onChange={this.handleDateChange}
                        />
                        <br />
                        <h2>Species:</h2>
                        <select name={"species"} style={{width: '200px', 'font-size': '16px'}}>
                            <option value={"default"} selected hidden>Select Species</option>
                            {
                                Object.values(this.props.classifications).map(function(value) {
                                    return <option value={value}>{value}</option>
                                }.bind(this))
                            }
                        </select>
                        <br /><br /><br /><br />
                        <button style={{width: 200}}
                                onClick={(e) => {e.preventDefault(); this.props.advanced()}}>
                            Advanced
                        </button>
                        <br /><br />
                        <button style={{width: 200, position: 'relative', bottom: '0px'}}>Submit</button>
                    </form>
                </div>
                <div style={{float: 'left'}}>
                    { this.props.mapOpen &&
                        <div style={{position: 'relative', left: '20px',width: '640px', height: '320px', backgroundColor: 'lightgreen'}}>
                            <LocationSelector
                                position={this.state.pos}
                                onChange={this.handleLocationChange}
                                radius={this.state.radius}
                            />
                        </div>
                    }
                </div>
            </div>
        )
    }

    handleDateChange(new_date){
        this.setState({
            date: new_date
        });
    }

    handleLocationChange({ position, address }){
        this.setState({
            loc: address,
            pos: position
        });
    }

    handleTextChange(e) {
        this.setState({ loc: e.target.value });
    }

    handleSubmit(e){
        e.preventDefault();

        this.props.onChange({
            lat: this.state.pos['lat'],
            lng: this.state.pos['lng'],
            manual_location: 1,
            radius: this.state.radius,
            date: this.state.date
        })
    }

    handleClickOutside(evt){
        if(evt.target.id == 'Location') return;

        this.props.setMapOpen(false);
    };
}

export default onClickOutside(Input);



