import superagent from 'superagent'

//Takes an image url and encodes the contents of the image, applying it to the function 'callback'
function convertUrlToString(src, callback){
    var xhttp = new XMLHttpRequest();
    xhttp.responseType = 'blob';
    xhttp.open('GET', src, true);

    xhttp.onload = function(e) {
        var fileReader = new FileReader();
        fileReader.onloadend = function() {
            callback(fileReader.result);
        }
        fileReader.readAsDataURL(xhttp.response);
    };

    xhttp.send();
}

//Accepts image url's and sends them off to the back end to analyse, returning the result to callback
export function analyse_images(imageUrl, value, callback){
    var encodings = []

    for(var i in imageUrl){
        var index = i //Loop may end before the function call below does, therefore store the index
        convertUrlToString(imageUrl[i], function(dataURL){
            dataURL = dataURL.replace("data:image/jpeg;base64,", "")
            dataURL = dataURL.replace("data:image/png;base64,", "")

            encodings = [...encodings, dataURL]

            //If at the end of the list, send the data to the back-end
            if(index == imageUrl.length-1){
                superagent
                    .post("/execute")
                    .send({"location": value.loc, "date": value.date, "url": encodings})
                    .then((result) => {callback(result.text)})
            }
        })
    }
}

//Accepts image url's and sends them off to the back end to analyse, returning the result to callback
export function submit_images(imageUrl, value, callback){
    var image_url_identifier = "image_url_"+imageUrl.length;

    superagent
        .post("http://api.questagame.com/api/sighting/new")
        .send({
            image_url_identifier: imageUrl,
            "sighting_dttm": "dttm",
            "lat": value.lat,
            "lng": value.lng,
            "device_type": "Web",
            "offline": "1",
            "manual_location": value.manual_location,
            "radius": value.radius
        })
        .then((result) => {console.log(result)});
}

export function get_dashboard(callback){
    superagent
        .get("/getdata")
        .end(function(_, values) {
            var data = JSON.parse(values.text);
            var categories = [];

            for(var i = 0, len=data['categories'].length; i<len; i+=1){
                categories.push(data['categories'][i]['name']);
            }

            callback(data['sighting_limit_per_day'] - data['sighting_count_per_day'], categories);
        })
}

export function get_location(callback){
    superagent
        .get("http://geoip.nekudo.com/api/full")
        .end(function(_, loc) {
            loc = loc.body;
            callback(
                loc.city.names.en+" "+loc.subdivisions[0].iso_code+", "+loc.country.names.en,
                {lat: loc.location.latitude, lng: loc.location.longitude}
            )
        })
}


