import React, { Component } from 'react'
import ReactDOM from 'react-dom/'
import Input from './components/Input'
import ImagePanel from './components/ImagePanel'
import DropComponent from "./components/DropComponent"
import AdvancedTab from "./components/AdvancedTab"

class App extends Component {
    constructor(){
        super()
        this.state = {
            imageUrl: [],
            formValue: '',
            loading: false,
            advancedTabOpen: false,
            mapOpen: false,
            apiData: [5, ["Birds", "Butterflies and Moths", "Crawling Critters", "Mammals, Reptiles, Amphibions",
                          "Buzzing Bugs and Beetles", "Sites on Map, Places, Nest Boxes", "Ferns and Fungi", "Weeds"]]
        }
        this.reset_page = this.reset_page.bind(this);
    }

    //Called whenever the user chooses to upload another file
    reset_page(e){
        e.preventDefault()

        //clean up any used resources
        for(var i in this.state.imageUrl){
            URL.revokeObjectURL(this.state.imageUrl[i])
        }

        this.setState({imageUrl: [], formValue: ''})
    }

    //Called whenever the user presses submit in the file Images.js
    add_images(value){
        this.setState({
            imageUrl: this.state.imageUrl.concat(value)
        })
    }

    //Called whenever the user sends off their images to the database
    submit_images(value){
        //Callback function to set state
        var apply_value = (x) => {
            this.setState({ formValue: x, loading: false })
        };

        apply_value(null);

        //submit_images(this.state.imageUrl, value, apply_value)
        //analyse_images(this.state.imageUrl, value, apply_value)
    }

    render(){
        //Render the page for the initial image drop
        if(this.state.imageUrl.length == 0){
            return(
                <div align="center" style={{height: "60%"}}>
                    <h4>Click below or drag your image!</h4>
                    <br />
                    <div style={{width: 280, height: 280, backgroundColor: 'lightgray'}}>
                        <div style={{height: '5%'}} />
                        <div style={{width: '90%', height: '90%'}}>

                            <DropComponent onChange={this.add_images.bind(this)}>
                                <img style={{backgroundColor: 'whitesmoke', height: '100%'}} src="static/src/ImageDrop.png" />
                            </DropComponent>
                        </div>
                    </div>
                </div>
            )
        }

        //Render the loading state
        else if(this.state.loading){
            return(
                <div align="center">
                    <br />
                    <h3>Analysing Photos...</h3>
                </div>
            )
        }

        //Render the page after the initial image drop
        else if(this.state.formValue === ''){
            //Rendering the background and form elements
            return(
                <div align="center">
                    <br />
                    <div style={{width:890, height:340, backgroundColor: 'lightgray'}}>
                        <div style={{float: 'left', margin:10}}>
                            <Input onChange={(x) => {this.setState({loading: true}); this.submit_images(x)}}
                                   advanced={()=>{(this.setState({advancedTabOpen: !this.state.advancedTabOpen}))}}
                                   setMapOpen={(x)=>{this.setState({mapOpen: x})}}
                                   mapOpen={this.state.mapOpen}
                                   classifications={this.state.apiData[1]}
                                   maxPhotos={this.state.apiData[0]}
                            />
                        </div>
                        {!this.state.mapOpen &&
                            <div style={{float: 'left', margin:10}}>
                                {this.state.imageUrl.length < 5 ? (
                                    <DropComponent onChange={(x) => this.add_images(x)}>
                                        <ImagePanel images={this.state.imageUrl} dimensions={[620, 320]} dropEnabled/>
                                    </DropComponent>)
                                :(
                                    <div>
                                        <DropComponent />
                                        <ImagePanel images={this.state.imageUrl} dimensions={[620, 320]} />
                                    </div>
                                )}
                            </div>
                        }

                    </div>
                    {this.state.advancedTabOpen &&
                        <div style={{width:890, height:200, backgroundColor: 'lightgray'}}>
                            <AdvancedTab />
                        </div>
                    }
                </div>
            )
        }

        //Render the final page
        else{
            return(
                <div align="center">
                    <br />
                    <ImagePanel images={this.state.imageUrl} dimensions={[620, 310]} />
                    <br />
                    <br />
                    <h3>{this.state.formValue}</h3>
                    <br /><br />

                    <form
                        onSubmit={this.reset_page}
                    >
                        <button>
                            Upload another photo
                        </button>
                    </form>
                </div>
            )
        }

    }
}

ReactDOM.render(<App />, document.getElementById('root'))


